import React from 'react';
import Layout from '../../components/layout'
import MoneyPrediction from '../../components/calculators/MoneyPrediction'
import SEO from "../../components/seo"


class MoneyPredictionPage extends React.PureComponent {
  constructor() {
    super();
    this.state = {
      monthlySalary: 1000,
      monthlyExpenses: 700,
      startingPoint: 2000,
      expInputs: [],
      predictForYears: 10,
      error: {},
      expectedExpenses: {},
    }
  }

  createArrayOfInputs = () => {

    return this.state.expInputs.map(i => {
      const onChangeValue = (e) => {
        this.setState({
          expectedExpenses: {
            ...this.state.expectedExpenses,
            [i]: {
              year: (this.state.expectedExpenses[i] || {}).year || "",
              value: e.target.value,
            }
          }
        });
      };
      const onChangeYear = (e) => {
        this.setState({
          expectedExpenses: {
            ...this.state.expectedExpenses,
            [i]: {
              year: e.target.value,
              value: (this.state.expectedExpenses[i] || {}).value || 0,
            }
          }
        });
      };

      return (
        <div key={i}>
          <span>In</span>
          <input
            onChange={onChangeYear}
            value={(this.state.expectedExpenses[i] || {}).year || ""}
            placeholder={"Year"}
          />
          <span> I am going to spend </span>
          <input
            onChange={onChangeValue}
            value={(this.state.expectedExpenses[i] || {}).value || ""}
            placeholder={"Value"}
          />
          {' '}<span onClick={() => this.removeExpInput(i)}>X</span>
        </div>
      );
    });
  }

  changeMonthlySalary = (e) => this.setValueWithCheck('monthlySalary', e);
  changeMonthlyExpenses = (e) => this.setValueWithCheck('monthlyExpenses', e);
  changePredictYears = (e) => this.setValueWithCheck('predictForYears', e);
  changeStartingPoint = (e) => this.setValueWithCheck('startingPoint', e);

  addExpInput = (e) => this.setState({
    expInputs: [...this.state.expInputs, Math.ceil(Math.random() * 100000)]
  });

  removeExpInput = (num) => {
    const newExpectedExpenses = {
      ...this.state.expectedExpenses,
    };
    delete newExpectedExpenses[num]
    this.setState({
      expInputs: this.state.expInputs.filter(item => item !== num),
      expectedExpenses: newExpectedExpenses,
    })
  };

  setValueWithCheck = (key, e) => {
    const conversion = parseInt(e.target.value);
    if (isNaN(conversion)) {
      this.setState({ error: {...this.state.error, [key]: true }, [key]: e.target.value });
    } else {
      const copy = {...this.state.error};
      delete copy[key];
      this.setState({ error: copy, [key]: conversion });
    }
  };

  render() {
    return (
      <Layout withFooter={false}>
        <div>
          <SEO
            title={"Future balance calculator."}
            description={"Struggling with money? Unclear future? Let's compute your future money balance. Add your income, expenses, expected expenses and let's look what the future balance will look like."}
            keywords={["money","calculator","expenses","income","future"]}
          />
          <div>How much money do you actually have? (This is starting point)</div>
          <input value={this.state.startingPoint} onChange={this.changeStartingPoint} />
          <hr />
          <div>Expected monthly salary (after tax)</div>
          <input value={this.state.monthlySalary} onChange={this.changeMonthlySalary} />
          <hr />
          <div>Your monthly expenses ?</div>
          <input value={this.state.monthlyExpenses} onChange={this.changeMonthlyExpenses} />
          <hr />
          <div>For how much years you want prediction to compute ?</div>
          <input value={this.state.predictForYears} onChange={this.changePredictYears} />
          <hr />
          <div>Do you have expected expenses at some year?</div>
          {this.createArrayOfInputs()}
          <button onClick={this.addExpInput}>Add expense</button>
          <hr />
          {
            Object.keys(this.state.error).length === 0 && (
              <MoneyPrediction
                monthSalary={this.state.monthlySalary}
                monthExpenses={this.state.monthlyExpenses}
                startingPoint={this.state.startingPoint}
                time={this.state.predictForYears}
                theoreticalExpenses={Object.values(this.state.expectedExpenses).reduce((acc, val) => {
                  acc[val.year] = acc[val.year] === undefined ? +val.value : (+acc[val.year] + (+val.value));
                  return acc;
                }, {})}
              />
            )
          }

        </div>
      </Layout>
    );
  }
 }

export default MoneyPredictionPage;
