import React from 'react';
import PropTypes from 'prop-types'
import { LineChart, XAxis, YAxis, CartesianGrid, Tooltip, Line, Legend, ResponsiveContainer } from 'recharts';

const chartMargin = {top: 5, right: 30, left: 20, bottom: 5};

class MoneyPrediction extends React.PureComponent {

   static propTypes = {
     monthSalary: PropTypes.number,
     monthExpenses: PropTypes.number,
     theoreticalExpenses: PropTypes.object,
     startingPoint: PropTypes.number,
      time: PropTypes.number,
   };

   getPredictedData = (year, n=10) => {

      const preds = [{ year, balance: this.props.startingPoint }];

      for (let i = year + 1; i < year + n + 1; i++) {
         let balance = preds[preds.length - 1].balance;
         balance += this.props.monthSalary * 12;
         balance -= this.props.monthExpenses * 12;

         if (this.props.theoreticalExpenses) {
            if (i.toString() in this.props.theoreticalExpenses) {
               balance -= this.props.theoreticalExpenses[i]
            }
         }

         preds.push({
            year: i,
            balance: balance
         })
      }

      return preds
   };

   render() {

      const nowDate = new Date();
      const nowYear = nowDate.getFullYear();
      const predictions = this.getPredictedData(nowYear, this.props.time);

      return (
            <ResponsiveContainer width={"100%"} height={400}>
               <LineChart data={predictions} margin={chartMargin}>
                  <XAxis dataKey="year"/>
                  <YAxis domain={[0, 'auto']} />
                  <CartesianGrid strokeDasharray="3 3"/>
                  <Tooltip/>
                  <Legend />
                  <Line type="monotone" dataKey="balance" stroke="#8884d8"/>
               </LineChart>
            </ResponsiveContainer>
      )
   }
}

export default MoneyPrediction;
